:root {
  --blue-color: #00acdc;
}

* {
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}

header {
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 50px;
  display: flex;
}

.header__logo_link {
  width: 40px;
  height: 40px;
}

.header__nav_items {
  padding: 0;
  display: flex;
}

.header__nav_item {
  justify-content: center;
  margin: 0 20px;
  list-style-type: none;
  display: flex;
}

.header__nav_item:first-child {
  margin: 0 20px 0 0;
}

.header__nav_item:last-child {
  margin: 0 0 0 20px;
}

.header__nav_item a.is-active:after {
  content: "";
  background-color: var(--blue-color);
  width: 100%;
  height: 4px;
  position: absolute;
  top: 35px;
  left: 0;
}

.header__nav_item a {
  color: #000;
  text-align: center;
  height: 15px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
  position: relative;
}

.header__nav_item a.is-active, .header__nav_item a:hover {
  color: var(--blue-color);
}

